html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
ul,
li {
  list-style: none;
}
button,
input,
select,
textarea {
  margin: 0;
}
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*,
*before,
*after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
img,
embed,
iframe,
object,
audio,
video {
  height: auto;
  max-width: 100%;
}
iframe {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
  text-align: left;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
body {
  min-width: 1200px;
}
/* 自定义原生 Chrome 滚动条 */
::-webkit-scrollbar {
  width: 8px;
  height: 9px;
}
::-webkit-scrollbar-track-piece {
  background-color: transparent;
  border-radius: 20px;
}
::-webkit-scrollbar-thumb {
  height: 10px;
  -webkit-transform: rotate3d(0, 0, 0, 0);
  transform: rotate3d(0, 0, 0, 0);
  -webkit-transition: background-color 100ms ease-out;
  -o-transition: background-color 100ms ease-out;
  transition: background-color 100ms ease-out;
  background-color: rgba(38, 38, 38, 0.3);
  border-radius: 20px;
}
::-webkit-scrollbar-thumb:vertical,
::-webkit-scrollbar-thumb:horizontal {
  width: 8px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(38, 38, 38, 0.4);
  width: 8px;
}
.common_page_placeholder {
  text-align: center;
  line-height: 70vh;
  font-size: 40px;
  text-transform: uppercase;
  font-weight: bold;
}

.layout_base .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
}
.layout_base .trigger:hover {
  color: #1890ff;
}

